body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;overflow:hidden}.ContactImage{margin:20px}.App-logo{height:40vmin;pointer-events:none}.ProjectsLink{color:inherit}.ProjectsLink:hover{color:inherit;text-decoration:inherit}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

html{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-rendering:optimizelegibility}.splash{position:relative;width:100%;height:500px;overflow:hidden}.splash-overlay,.splash-video{position:absolute;margin:0px;padding:0px;top:0px;left:0px}.splash-overlay{z-index:10;height:100%;width:100%}.splash-svg{width:100%;height:100%}.splash-background{fill:#fff;width:100%;height:100%}.splash-text{text-anchor:middle;font-weight:900;letter-spacing:-5px;font-size:1300%;font-family:"Brush Script MT","Century Gothic",'Open Sans','Papyrus', sans-serif}.splash-masker{x:0;y:0;width:100%;height:100%;-webkit-mask:url(#splash-mask);mask:url(#splash-mask)}.splash-video{height:100%;width:100%;overflow:hidden}.splash-bg-video{width:100% !important;height:auto !important;position:absolute}

.UnsplashPhoto{height:auto;width:100%;position:relative;background-size:cover}.UnsplashPhoto #endrights{position:absolute;bottom:0px;padding:3px;right:0px;color:rgba(83,83,83,0.9)}.UnsplashPhoto .UnsplashLink{position:absolute;bottom:0px;left:0px;background-color:black;color:white;text-decoration:none;padding:4px 6px;font-family:BlinkMacSystemFont,Helvetica, Ubuntu, Roboto, Noto, Arial, sans-serif;font-size:12px;font-weight:bold;line-height:1.2;display:inline-block;border-radius:3px}.UnsplashPhoto .UnsplashSpan{display:inline-block;padding:2px 3px}.UnsplashPhoto .UnsplashSpan svg{height:12px;width:auto;position:relative;vertical-align:middle;top:-1px;fill:white}

#FallingCubesTop .mjx-mrow span{font-size:120%}

